.nav {
    grid-area: n;
    background-color: #cdc0af;
    padding: 20px;
    margin-right: 5px;
    border-radius: 5px;
    border-right: #e5945b solid 5px;
    border-top: #e5945b solid 5px;
    border-left: #9fbaac solid 5px;
    border-bottom: #9fbaac solid 5px;
}

.item a {
    color: white;
    text-decoration: none;
}

.item a.activeLink {
    color: gold;
}

.friends {
    margin: 10px;
    text-transform: uppercase;
    font-size: 20px;
    text-align: center;
}